                <p>Nous crons un schma de donnes qui contient les champs souhaits pour chaque Objet, On indique leur type ainsi que leur caractre. Pour cela, on utilise la mthode Schema de Mongoose. l'Id est automatiquement gnr par Mongoose ;</p>

